import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

@Component({
  tag: 'time-input',
  styleUrl: 'time-input.css',
  shadow: true,
})
export class TimeInput {
  /**
   * The name of the input field.
   */
  @Prop() name!: string;

  /**
   * The label for the input field.
   */
  @Prop() label!: string;

  /**
   * The current value of the time input.
   */
  @Prop({ mutable: true, reflect: true }) value: string = '';

  /**
   * The minimum allowed time (HH:MM).
   */
  @Prop() min?: string;

  /**
   * The maximum allowed time (HH:MM).
   */
  @Prop() max?: string;

  /**
   * The step interval in seconds.
   */
  @Prop() step?: number;

  /**
   * Whether the input is disabled.
   */
  @Prop() disabled: boolean = false;

  /**
   * Event emitted when the input value changes.
   */
  @Event() myChange: EventEmitter<string> | undefined;

  private handleChange = (event: Event) => {
    if (this.disabled) {
      return;
    }
    const input = event.target as HTMLInputElement;
    this.value = input.value;
    this.myChange?.emit(this.value);
  };

  render() {
    return (
      <div class={{ 'time-wrapper': true, 'time-wrapper--disabled': this.disabled }}>
        <label htmlFor={this.name} class="time-label">{this.label}</label>
        <input
          type="time"
          id={this.name}
          name={this.name}
          value={this.value}
          min={this.min}
          max={this.max}
          step={this.step}
          disabled={this.disabled}
          onChange={this.handleChange}
          class="time-field"
          placeholder="HH:MM"
        />
      </div>
    );
  }
}
